import React from 'react';
import Container from '@mui/material/Container';
import { light, dark } from './style';

const Section = ({ children, id, sx = {}, maxWidth = 'lg', theme = 'light' }) => {
  const isLight = theme === 'light';
  const isFull = maxWidth === false;

  return (
    <Container
      id={id}
      maxWidth={false}
      disableGutters={isFull}
      sx={{
        py: 9,
        backgroundColor: isLight ? light : dark,
        color: isLight ? dark : light,
        ...sx
      }}
    >
      <Container
        maxWidth={maxWidth}
        disableGutters={isFull}
      >
        {children}
      </Container>
    </Container>
  );
};

export default Section;
